<template>
  <div>
    <!-- container布局容器 -->
    <el-container class="container">
      <el-aside width="200px">
          <!-- NAVMENU导航菜单 -->
        <el-row class="tac">
          <el-col :span="24">
            <el-menu
              router
              unique-opened
              default-active="2"
              class="el-menu-vertical-demo"
              background-color="rgb(32,34,42)"
              text-color="#fff"
              active-text-color="#ffd04b"
            >
            <el-menu-item index="/index">
                <i class="el-icon-menu"></i>
                <span slot="title">首页</span>
              </el-menu-item>
              <div v-for="item in userinfo.menus" :key="item.id">
              <el-submenu :index="item.id+''" v-if="item.children">
                <template slot="title">
                  <i :class="item.icon"></i>
                  <span>{{item.title}}</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item :index='"/index"+idd.url' v-for="idd in item.children" :key="idd.id">{{idd.title}}</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
                <el-menu-item :index="'/index'+item.url" v-else>
                    <span>{{item.title}}</span>
                </el-menu-item>
              </div>

            </el-menu>
          </el-col>
        </el-row>
        <!-- NAVMENU导航菜单 -->
      </el-aside>
      <el-container>
        <el-header>
          <div id="header">
            <span>{{userinfo.username}}</span>
            <el-button @click="logout">退出</el-button>
          </div>
        </el-header>
        <el-main>
          <!-- 面包屑 -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <!-- 面包屑 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
    <!-- 布局容器 -->
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  props: [],
  components: {},
  data() {
    return {

    };
  },
  computed: {
    ...mapGetters({
      userinfo:"userinfo"
    }),
  },
  methods: {
    ...mapActions({
      reqchangeinfo:"reqchangeinfo"
    }),
    logout(){
      this.reqchangeinfo({})
      this.$router.push('/login')
    }
  },
  mounted() {
  },
};
</script>
<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
}

.el-aside {
  background-color: rgb(32,34,42);
  color: #333;
  text-align: center;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
}

body > .el-container {
  margin-bottom: 40px;
}

.container {
  height: 100vh;
}
#header{
  float: right;
}

</style>


